html,body{width:100%;height:100%;margin:0;padding:0;font-size:12px}.btn{display:inline-block;text-align:center;padding:0 15px;height:24px;line-height:24px;border:1px solid #ddd;border-radius:2px;cursor:pointer;-webkit-transition:all .1s;transition:all .1s;-webkit-user-select:none}.btn:hover{background-color:#eee}.btn:active{position:relative;top:2px}.btn-primary{background-color:#26a69a;color:#fff;border-color:#26a69a}.btn-primary:hover{background-color:#2bbbad}.btn-danger{background-color:#FB6E6E;color:#fff;border-color:#FB6E6E}.btn-danger:hover{background-color:#DC5454}


