.page-projects{position:relative;padding-top:30px}.page-projects .projects-list{position:absolute;top:30px;left:30px;width:300px;border:1px solid #ddd;border-radius:2px}.page-projects .projects-list .projects-actions{padding:15px;border-bottom:1px solid #ddd}.page-projects .projects-list .preloader{text-align:center;padding:15px}.page-projects .projects-list ul{list-style:none;padding:0;margin:0}.page-projects .projects-list li{height:40px;line-height:40px;padding:0 15px;border-bottom:1px solid #ddd;-webkit-transition:all .1s;transition:all .1s}.page-projects .projects-list li:hover{background-color:#f8f8f8}.page-projects .projects-list li:hover .actions{visibility:visible}.page-projects .projects-list li.active{border-left:4px solid #0DE4EF}.page-projects .projects-list li .name{display:inline-block;cursor:pointer}.page-projects .projects-list li .actions{visibility:hidden;float:right}


