.flowchart-block{position:absolute;z-index:999;border:1px solid #333;-webkit-user-select:none}.flowchart-block .name{text-align:center;padding-top:30%}.flowchart-block .link-begin,.flowchart-block .link-end{display:none;cursor:pointer;z-index:9999;background-color:#fff;position:absolute;left:50%;margin-left:-10px;width:20px;height:20px;border-radius:50%;border:1px solid #333}.flowchart-block .link-begin{bottom:-11px}.flowchart-block .link-end{top:-11px}.flowchart-block:hover .link-begin{display:block}.flowchart-block.active{cursor:move}.flowchart-block.link-active .link-begin{display:block !important}.flowchart-block.link-active .link-begin:after{position:absolute;content:'';top:5px;left:5px;width:10px;height:10px;border-radius:50%;background-color:#000}.flowchart-block.link-active .link-end{display:none !important}.linking .flowchart-block .link-begin{display:none}.linking .flowchart-block .link-end{display:block}


